import { createStore } from 'vuex'
import axios from 'axios'

export default createStore({
  state: {
    bLogin:false,
    onUser:''
  },
  mutations: {

  },
  actions: {
    async findUser(context, playload){
      try{
        let {status, data} = await axios
        ({
          method: 'POST',
          url: 'http://172.30.1.12:8080/find',
          data:JSON.stringify(playload),

          headers:{
            'Content-Type': 'application/json'
          }
          });
          if(data.login == 'true'){
            context.state.bLogin = true;
            // console.log(data);
            context.state.onUser = data.userName;
          }
          else {
            alert('login에 실패했습니다.');
          }
         // console.log(data.login);

        // console.log(status);
      }
      catch(e){
        console.log(e);
      }
    },
    async insertUser(context, playload){
      console.log(playload);
      let {status, data} = await axios
      ({
        method: 'POST',
        url:'http://172.30.1.12:8080/insert',
        data:JSON.stringify(playload),
        headers:{
          'Content-Type': 'application/json'
        }
      });
      console.log(status);
      console.log(data);
    }
  },
  modules: {
  }
})
